import { Navigate } from 'react-router-dom';
import { useAuth } from '../hooks';

/**
 * 1.    API Route
 *  2.    Route
 * -      ,  
 * -     Redirect  redirectTo
 */

export default function PrivateRoute({ component, redirectTo = '/' }) {
  const { isLoggedIn } = useAuth();
  return isLoggedIn ? component : <Navigate to={redirectTo} />;
}
